<template>
　　<pdf :src="$store.state.read_pdf + original"></pdf>
</template>

<script>
import pdf from 'vue-pdf'
export default {
    components: {
        pdf
    },
    data(){
        return{
            original:"",//文件名
        }
    },
    methods:{
        // 获取资料细信息
        getData() {
            this.$axios.post("/api/database-list",{
                id:this.$route.params.id
            }).then((res) =>{
                this.original=res.data.data.original_file
            }).catch((error) =>{
//                console.log(error);
            });
        },
    },
    created(){
        this.getData();
    },
    mounted(){
    // console.log(this.$route.params.id);

    }
}

</script>
<style lang="less" rel="stylesheet/less">
@import "../../../static/css/base.css";
</style>



